import { AfterViewInit, ChangeDetectorRef, Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { InputTableDataSource, InputTableItem } from './input-table-datasource';
import { SelectionModel } from '@angular/cdk/collections';
import { HttpClient } from '@angular/common/http';
import { Flightinput } from './flightinput';
import { Observable } from 'rxjs/internal/Observable';

@Component({
  selector: 'input-table',
  templateUrl: './input-table.component.html',
  styleUrls: ['./input-table.component.css']
})
export class InputTableComponent implements AfterViewInit {
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatTable) table!: MatTable<InputTableItem>;
  dataSource: InputTableDataSource;

  /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
  displayedColumns = ['datum', 'behoerdennummer', 'abflugort', 'ankunftsort', 'befoerderungsklasse', 'personenAnzahl', 'emissionen', 'select'];

  constructor(private http: HttpClient) {
    this.dataSource = new InputTableDataSource();
  }

  ngOnInit() {
    this.updateData();
    this.table.renderRows();
  }

  updateData(): void {
    const dataObserver: Observable<Flightinput> = this.http.get<Flightinput>("/api/input/view");
    dataObserver.subscribe(data => {
      data.data.forEach(elem => {
        this.dataSource.data.push({
          datum: elem.startingDate,
          behoerdennummer: elem.authorityNumber,
          abflugort: elem.iataStart,
          ankunftsort: elem.iataDest,
          befoerderungsklasse: elem.travelClass.toUpperCase(),
          personenAnzahl: elem.number,
          emissionen: elem.emission
        })
      })
    });
  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
    this.table.dataSource = this.dataSource;
  }

  /** for the Toggles */
  updateActiveStatus(row: { activate: boolean; }) {
    row.activate = !row.activate;
  }

  selection = new SelectionModel<InputTableItem>(true, []);

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: InputTableItem): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.behoerdennummer + 1}`;
  }




}
