import { HttpClient } from '@angular/common/http';
import { AfterViewInit, Component, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { Observable } from 'rxjs/internal/Observable';
import { Flightinput } from '../../inputs/input-table/flightinput';
import { DashboardTableDataSource, DashboardTableItem } from './dashboard-table-datasource';

@Component({
  selector: 'dashboard-table',
  templateUrl: './dashboard-table.component.html',
  styleUrls: ['./dashboard-table.component.css']
})
export class DashboardTableComponent implements AfterViewInit {
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  @ViewChild(MatTable) table!: MatTable<DashboardTableItem>;
  dataSource: DashboardTableDataSource;

  /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
  displayedColumns = ['datum', 'behoerdennummer', 'abflugort', 'ankunftsort', 'befoerderungsklasse', 'personenAnzahl', 'emissionen'];


  constructor(private http: HttpClient) {
    this.dataSource = new DashboardTableDataSource();

  }

  ngOnInit() {
    this.updateData();
    this.table.renderRows();
  }

  updateData(): void {
    this.dataSource.data = [];
    const dataObserver: Observable<Flightinput> = this.http.get<Flightinput>("/api/dashboard");
    dataObserver.subscribe(data => {
      data.data.forEach(elem => {
        this.dataSource.data.push({
          datum: elem.startingDate,
          behoerdennummer: elem.authorityNumber,
          abflugort: elem.iataStart,
          ankunftsort: elem.iataDest,
          befoerderungsklasse: elem.travelClass.toUpperCase(),
          personenAnzahl: elem.number,
          emissionen: elem.emission
        })
      })
    });
  }

  ngAfterViewInit(): void {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
    this.table.dataSource = this.dataSource;
  }
}
